import { createClient } from '@supabase/supabase-js';
import { User, Category, Spreadsheet, Subscription, Download } from '@/types';

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!;

export const supabase = createClient(supabaseUrl, supabaseAnonKey);

// Database types for better TypeScript support
export interface Database {
  public: {
    Tables: {
      users: {
        Row: User;
        Insert: Omit<User, 'id' | 'created_at'>;
        Update: Partial<Omit<User, 'id' | 'created_at'>>;
      };
      categories: {
        Row: Category;
        Insert: Omit<Category, 'id' | 'created_at'>;
        Update: Partial<Omit<Category, 'id' | 'created_at'>>;
      };
      spreadsheets: {
        Row: Spreadsheet;
        Insert: Omit<Spreadsheet, 'id' | 'created_at' | 'updated_at'>;
        Update: Partial<Omit<Spreadsheet, 'id' | 'created_at' | 'updated_at'>>;
      };
      subscriptions: {
        Row: Subscription;
        Insert: Omit<Subscription, 'id' | 'created_at' | 'updated_at'>;
        Update: Partial<Omit<Subscription, 'id' | 'created_at' | 'updated_at'>>;
      };
      downloads: {
        Row: Download;
        Insert: Omit<Download, 'id'>;
        Update: Partial<Omit<Download, 'id'>>;
      };
    };
  };
}

// Auth helpers
export const signUp = async (email: string, password: string, name: string) => {
  const { data, error } = await supabase.auth.signUp({
    email,
    password,
    options: {
      data: {
        name,
      },
    },
  });
  return { data, error };
};

export const signIn = async (email: string, password: string) => {
  const { data, error } = await supabase.auth.signInWithPassword({
    email,
    password,
  });
  return { data, error };
};

export const signOut = async () => {
  const { error } = await supabase.auth.signOut();
  return { error };
};

export const getCurrentUser = async () => {
  const { data: { user }, error } = await supabase.auth.getUser();
  return { user, error };
};

// Database helpers
export const getCategories = async () => {
  const { data, error } = await supabase
    .from('categories')
    .select('*')
    .order('name');
  return { data, error };
};

export const getSpreadsheets = async (categoryId?: string | null, search?: string, limit = 20, offset = 0) => {
  let query = supabase
    .from('spreadsheets')
    .select(`
      *,
      category:categories(*)
    `)
    .order('created_at', { ascending: false })
    .range(offset, offset + limit - 1);

  if (categoryId) {
    query = query.eq('category_id', categoryId);
  }

  if (search) {
    query = query.or(`title.ilike.%${search}%,description.ilike.%${search}%,tags.cs.{${search}}`);
  }

  const { data, error } = await query;
  return { data, error };
};

export const getSpreadsheetById = async (id: string) => {
  const { data, error } = await supabase
    .from('spreadsheets')
    .select(`
      *,
      category:categories(*)
    `)
    .eq('id', id)
    .single();
  return { data, error };
};

export const getUserSubscription = async (userId: string) => {
  const { data, error } = await supabase
    .from('subscriptions')
    .select('*')
    .eq('user_id', userId)
    .eq('status', 'active')
    .single();
  return { data, error };
};

export const createDownloadRecord = async (userId: string, spreadsheetId: string) => {
  const { data, error } = await supabase
    .from('downloads')
    .insert({
      user_id: userId,
      spreadsheet_id: spreadsheetId,
      downloaded_at: new Date().toISOString(),
    });
  return { data, error };
};

export const getUserDownloads = async (userId: string) => {
  const { data, error } = await supabase
    .from('downloads')
    .select(`
      *,
      spreadsheet:spreadsheets(*)
    `)
    .eq('user_id', userId)
    .order('downloaded_at', { ascending: false });
  return { data, error };
};
